<script>
import { mapState } from 'vuex';
import VueCropper from 'vue-cropperjs';
import 'cropperjs/dist/cropper.css';
import Compressor from 'compressorjs';

export default {
  name: 'mainBody',
  data(){
      return{
          isChoosingPic:false,
          uploadTitle:'',
          uploadDescription:'',
          imageSrc:'',
          isDoneChoosingImage:false,
          ImageType: '',
          ImageName: '',
          showFloatButton:false,
          showLoadingFromButtonDivWhenLoading:false,
      }  
  },   
    components: {
        VueCropper,
    },
    computed:{

        ...mapState({
            user:'auth',
        }),

        /*comboHasNotLoginAndNotDoneChoos:function(){
            if(this.user === null){
                return false
            }else{
                return true
            }
        },*/

        notAllowedToUpload:function(){
            if(this.imageSrc.length === 0 || !this.imageSrc.trim()){
                return true
            }
            if(this.uploadTitle.length === 0 || !this.uploadTitle.trim()){
                return true
            }
            if(this.uploadDescription.length === 0 || !this.uploadDescription.trim()){
                return true
            }
            else{
                return false
            }
        }
    },  
    methods: {
        isChooseImage:function(e){
            const file = e.target.files[0];

            this.ImageType = file.type;
            this.ImageName = file.name;

            if (file.type.indexOf('image/') === -1) {
                alert('Please select an image file');
                return;
            }
            if (typeof FileReader === 'function') {
                const reader = new FileReader();
                    reader.onload = (event) => {
                    
                    this.imageSrc = event.target.result;
                    this.$refs.ucropper.replace(event.target.result);

                };
                reader.readAsDataURL(file);
                this.isDoneChoosingImage = true
            } else {
                alert('Sorry, FileReader API not supported');
            }

        },
       upload:function(){        
           this.showLoadingFromButtonDivWhenLoading  = true
           this.showFloatButton = false

           let self = this

           this.cropImgTwo = this.$refs.ucropper.getCroppedCanvas().toBlob((blob) => {
                var blobToFile = new File([blob], this.ImageName, {type: this.ImageType, lastModified: Date.now()});
                var Data = new FormData();

                var settings = { 
                    headers:{
                        'content-type':'multipart/form-data',
                        'authorization': `gop-tok ${self.user.access_token}`
                    }
                }

                new Compressor(blobToFile, {
                    quality: 0.5,
                    success(result){

                        var type = result.type
                        var realTypeR = type.split("/");
                        var name = "image."+realTypeR[1];
                        var realFile = new File([result], name, {type: type, lastModified: Date.now()});

                        Data.append('file', realFile, self.ImageName);
                        Data.append('title', self.uploadTitle);
                        Data.append('description', self.uploadDescription);

                        async function Uploadpic(){
                            try {

                                const dataFromAsync = await self.$http.post('http://127.0.0.1:5000/upload',Data,settings)
                                console.log(dataFromAsync)
                                self.showLoadingFromButtonDivWhenLoading = false
                                self.showFloatButton = true
                                self.isChoosingPic = false
                                self.isDoneChoosingImage = false
                                console.log('success')
                                //storex.commit("changeOrgainImage",dataFromAsync.data);
                        
                            } catch (error) {
                                self.showLoadingFromButtonDivWhenLoading = false
                                self.showFloatButton = true
                                self.isChoosingPic = false
                                self.isDoneChoosingImage = false
                                console.log(error)
                                console.log('error')
                                //storex.commit("changeOrgainImage",error.response.data.errors);
                            }
                        }

                        Uploadpic()

                    },
                    error(err) {
                        console.log(err.message);
                        alert("There was an error processing your image, This does not happen often,Try checking your image or send us a feedback if it continues");
                    },
                        
                });

            }, 'image/jpeg' );
            
       },
       isWantingUpload:function(){
           this.isChoosingPic = true
       },
       cancelUpload:function(){
           this.isChoosingPic = false
           this.isDoneChoosingImage = false
       },
       showFloatButtonM:function(){
           setTimeout(() => {
               this.showFloatButton = true
           }, 2000);
       },
        
    }, 
    mounted(){
        this.showFloatButtonM()
    }

}
</script>

