import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    auth:null,
  },
  mutations: {

    getAuth(state){
      const authStatus = localStorage.getItem("auth")
      if(!authStatus){
        state.auth = null  
      }else{
        state.auth = JSON.parse(authStatus)
      }

    },

    loggedInSaveAuth(state,payload){
      state.auth = payload
      localStorage.setItem("auth",JSON.stringify(payload))
      
    },

    errorLogginIn(state){
      state.auth = null
      localStorage.removeItem('auth')
    }

  },
  actions: {
    
  },
  modules: {
    
  }
})
