<template>
    <div>
        
        <mainheader />

        <keep-alive>
            <component v-bind:is="component" />
        </keep-alive>

    </div>
</template>

<script>
    import mainheader from '../components/mainheader.vue'
    import mainBody from '../components/mainBody.vue'
    import Contact from './Contact.vue'

    export default {
        name: 'index',
        components: {
            mainheader,
            mainBody,
            Contact,
        },
        computed:{

            component:function(){
                
                if(this.$route.path === "/"){
                    return "mainBody"
                }
                if(this.$route.path === "/contact"){
                    return "Contact"
                }
                return false
            }
           
        },

    }
</script>
